#village_center {
    position: relative;
    text-align: center;
    max-width: 100vw;
}

#village_center>.title {
    text-align: right;
    max-width: 200px;
    overflow: hidden;
    margin-inline-start: 25px;
}

#village_center>.map {
    /* background-image: url(../images/Center.webp); */
    width: 540px;
    max-width: 100vw;
    height: 448px;
    position: relative;
}
.from-work #village_center>.map {
    opacity: 0.4;
}

#village_center>.map>.background {
    z-index: -1 !important;
    right: 0px;
    top: 0px;
    max-width: 100vw;
}

#village_center>.map>img {
    position: absolute;
    background-repeat: no-repeat;
    background-position: top right;
}

#village_center>.map>a>.building {
    width: 75px;
    height: 100px;
    z-index: 3;
    position: absolute;
}

#village_center>.map>.iso {
    background-image: url(../images/buildings/iso.gif);
}

